@charset "UTF-8";

body {
  margin: 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

button {
  width: 100px;
  height: 30px;
  line-height: 30px;
  padding: 0;
  text-align: center;
  background: #cccccc;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  transition: all .5s;

  &:hover {
    background: #000000;
    color: #ffffff;
  }
}

.open__modal {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  pointer-events: none;
  overflow-y: hidden;
  box-sizing: border-box;
  padding-right: 15px;

  @media screen and (max-width: 1199px) {
    padding-right: 0;
  }

  .overlay {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 999;
  }
}

.modal {
  position: absolute;
  opacity: 0;
  pointer-events: none;
  transition: 0.35s ease-in-out;;
  width: 300px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  box-sizing: border-box;
  border-radius: 4px;
  background-color: #fafafa;
  padding: 25px;

  &.open__modal__window {
    pointer-events: initial;
    z-index: 999;
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }

  .close-modal {
    content: '';
    display: block;
    position: absolute;
    width: 20px;
    height: 20px;
    right: 15px;
    top: 15px;
    background-image: url("data:image/svg+xml;base64,");
    background-size: contain;
    transition: all .5s;
    cursor: pointer;
    opacity: .5;

    &:hover {
      opacity: 1;
    }
  }
}
